import React from 'react'
import ProductContext from '../../contexts/Products/Context'

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';


const Products = () => {
	const { clients } = React.useContext(ProductContext)

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
          	<TableCell>Clientes</TableCell>
						<TableCell alignt="left">SKU</TableCell>
						<TableCell alignt="left">Bundle</TableCell>
            <TableCell alignt="left">Productos</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {clients.map((client) => (
            <TableRow
              key={client.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell align="left">{client.name}</TableCell>
              <TableCell align="left">{client.prefix}</TableCell>
              <TableCell align="left">{client.bundle}</TableCell>
              <TableCell align="left">{client.products.length}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
export default Products