import axios from './axios'
export const productsAPI = {
	list: (callback) => {
		axios
			.get('/products/')
			.then(r => callback(r.data))
			.catch(e => console.log("Error on products/list: ", e))
	},
	read: (id, callback) => {
		axios
			.get('/products/'+id)
			.then(r => callback(r.data))
			.catch(e => console.log("Error on products/detail: ", id, e))
	},
	assignClient: (body, callback) => {
		axios
			.put('/products/assignClient', body)
			.then(r => callback(r.data))
			.catch(e => console.log("Error on products/assignClient: ", body, e))
	}
}

export const clientsAPI = {
	create:(client, callback) => {
		axios
			.post('/clients/', client)
			.then(r => callback(r.data))
			.catch(e => console.log("Error on clients/create"))
	},
	list: (callback) => {
		axios
			.get('/clients/')
			.then(r => callback(r.data))
			.catch(e => console.log("Error on clients/list: ", e))
	},
}

export const liquidationsAPI = {
	create:(payments, callback) => {
		axios
			.post('/liquidations/', payments)
			.then(r => callback(r.data))
			.catch(e => console.log("Error on liquidations/create"))
	}
}