import * as React from 'react'
import Box from '@mui/material/Box'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'

function LinkTab(props) {
  return (
    <Tab
      component="a"
      onClick={(event) => {
        event.preventDefault()
      }}
      {...props}
    />
  )
}

export default function NavTabs(props) {
  const tabs = [
    { n:"Productos", l:'/products' },
    { n:"Clientes", l:'/clients'},
    { n:"Liquidaciones", l:'/liquidations'},
  ]
  const handleChange = (event, i) => props.history.push(tabs[i].l)
  const getCurrentTab = () => {
    let ct = 0
    tabs.forEach((t, i) => {
      if(window.location.pathname.indexOf(t.l) >= 0)
        ct = i
    })
    return ct
  }
  const [tab, setTab] = React.useState(getCurrentTab())

  React.useEffect(() => {
    setTab(getCurrentTab())
  }, [window.location.pathname])

  return (
    <Box sx={{ width: '100%' }}>
      <Tabs value={tab} onChange={handleChange} aria-label="nav tabs example" >
        {tabs.map((t) => (
          <LinkTab key={t.n} label={t.n} href={t.l} />
        ))}
      </Tabs>
    </Box>
  )
}