import React from 'react';
import Button from '@mui/material/Button';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import IconButton from '@mui/material/IconButton';
import { liquidationsAPI } from '../../api'
import { api_url } from '../../config'

const Liquidations = () => {
	const fileRef = React.useRef()
	const buttonRef = React.useRef()
	const formRef = React.useRef()
	
	const downloadFile = (res) => {
		window.open(api_url+res, "_blank")
	}

	const submitFile = (e) => {
		console.log("Submitting file")
		e.stopPropagation()
		e.preventDefault()
		if(fileRef.current.files.length){
			buttonRef.current.disabled = true
			const data = new FormData()
			data.append("payments", fileRef.current.files[0])
			console.log("Submitting file: ", e)
			liquidationsAPI.create(data, downloadFile)
		}
	}

	return (
		<div style={{
			display: 'flex',
			margin: 'auto',
			width: 400,
			flexWrap: 'wrap',
		}}>
			<div style={{ width: '100%', float: 'left' }}>
				<h3>Subir reporte de pagos</h3> <br />
			</div>
			<form onSubmit={submitFile} encType="multipart/form-data">
				<input
					type="file"
					id="contained-button-file"
					name="payment"
					ref={fileRef}
				/>
				<Button ref={buttonRef} variant="contained" color="primary" type="submit">
					Generar requisiciones
				</Button>
			</form>
		</div>
	)
}

export default Liquidations;
