import React from 'react'
import ProductsContext from './Context'
import { productsAPI, clientsAPI } from '../../api'
const ProductsProvider = (props) => {
	const [products, setProducts] = React.useState([])
	const [clients, setClients] = React.useState([])
	const [selectedProducts, setSelectedProducts] = React.useState(false)
	
	const _setSelectedProducts = (pids) => {
		if(pids && pids.length)
			setSelectedProducts(pids)
		else
			setSelectedProducts(false)
	}

	const assignClient = (cid) => {
		console.log("Assigning client ", cid, " to ", selectedProducts)
		productsAPI.assignClient({products:selectedProducts, client:cid}, (client) => {
			const changed = products
				.filter(p => selectedProducts.includes(p.id))
				.map(p => {
					return {...p, client:client}
				})
			setProducts([
				...products.filter(p => !selectedProducts.includes(p.id)),
				...changed
			])
		})
	}

	React.useEffect(() => {
		clientsAPI.list(setClients)
		productsAPI.list(setProducts)
	}, [])

	return (
		<ProductsContext.Provider value={{
			products,
			selectedProducts,
			setSelectedProducts: _setSelectedProducts,
			clients,
			assignClient,
		}}>
			{props.children}
		</ProductsContext.Provider>
	)
}
export default ProductsProvider